.portfolio {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    gap: 30px;
    padding: 40px;
}

.portfolio-item {
    background-color: #fff;
    border: 1px solid #ddd;
    padding: 20px;
    text-align: center;

    transition: transform 0.2s ease-in-out;
    border-radius: 5px;

    text-decoration: none;
}

.portfolio-item img {
    max-width: 100%;
    height: auto;
    border-radius: 15px;
    margin-bottom: 10px;
    object-fit: cover;
    object-position: center;
}

.main-projects {
    display: flex;
    justify-content: center;
    align-items: center;
}

.portfolio-item h3 {
    font-size: 1.5rem;
    margin: 0;
}

.portfolio-item p {
    font-size: 1rem;
    color: #777;
}

.portfolio-item:hover {
    transform: scale(1.05);
    border: 4px solid #e8f4d0;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    /* Add a hover effect */
}